<template>
  <div id="app">
    <h3>可增删改查的品牌列表</h3>
      <div class="top">
        <div class="left">
            <label>id <input  type="text" v-model.trim="id"></label>
            <label>Name <input type="text" v-model.trim="name"></label>
            <label>数量 <input type="text" v-model.trim="num"></label>
            <button @click="add">添加</button>
        </div>
        <div class="right">
            <div>
                <input type="text" v-model="search" placeholder="请输入需要搜索的内容">
            </div>
        </div>
      </div>
      <div class="bot">
        <table border="1" >
            <tr>
                <th>全选<input type="checkbox" v-model="isAllChecked"></th>
                <th>ID(id)</th>
                <th>NAME(名称)</th>
                <th>NUM(数量)</th>
                <th>CTIME(创建时间)</th>
                <th>OPERATION(更多操作)</th>
            </tr>
            <tr v-for="item in lists" :key="item.id">
              <td><input type="checkbox" v-model="item.isChecked"></td>
               <td>{{ item.id }}</td>
               <td>{{ item.name }}</td>
               <td>
                   <div v-if="!item.isShow" @click="showInput($event,item.id)">{{ item.num }}</div>
                   
                    <input @blur="item.isShow = !item.isShow" v-else type="text" v-model="item.num">
               </td>
               <td>{{ item.time }}</td>
               <td><button @click = "del(item.id)">删除</button></td>
            </tr>
            
                
             
        </table>
        
        
      </div>
      <div class="top">
        数量总计:{{  total }}
      </div>
  </div>
</template>
<script>
export default {
  data(){
                return {
                    id : "",
                    name : "",
                    search:"",
                    num:"",
                   list:[
                       {
                          id:1,
                          name:"张三" ,
                          time:"2019",
                          num:1,
                          isChecked:false,
                          isShow:false
                       },
                       {
                          id:2,
                          name:"李四" ,
                          time:"2020",
                          num:2,
                          isChecked:false,
                          isShow:false
                       },
                       {
                          id:3,
                          name:"王五" ,
                          time:"2020",
                          num:3,
                          isChecked:false,
                          isShow:false
                       },
                       {
                          id:4,
                          name:"赵六" ,
                          time:"2020",
                          num:4,
                          isChecked:false,
                          isShow:false
                       },
                   ]
                }
            },
            computed: {
                lists(){
                    var list1 = this.list.filter(ele => ele.name.includes(this.search) )
                    return list1;
                },
                isAllChecked:{
                  get(){
                    return this.list.every(ele => ele.isChecked == true)
                  },
                  set(val){
                    if(this.isAllChecked){
                      this.list.forEach(ele => ele.isChecked = false)
                    }else{
                      this.list.forEach(ele => ele.isChecked = true)
                    }
                  }
                },
                total(){
                  return this.list.reduce((total , current)=>{
                    if(current.isChecked){
                      return total = total + parseFloat(current.num);
                    }else{
                      return total 
                    }
                  },0)
                }
            },
            methods: {
                showInput(e,val){
                    var ele = e.target.parentNode.childNodes;
                    var ite = this.list.find(ele => ele.id == val);
                    ite.isShow = !ite.isShow;
                    setTimeout(()=>{
                        ele[0].focus();
                        console.log(ele[0])
                    })
                    
                },
                add(){
                    if(this.list.some(ele=> ele.id == this.id)){
                        alert("id不能相同!");
                        return ;
                    };
                    if(this.id == ""){
                        alert("id不能为空!");
                        return ;
                    };
                    if(this.name == ""){
                        alert("name不能为空!");
                        return ;
                    };
                    if(this.num == ""){
                        alert("数量不能为空!");
                        return ;
                    };
                    var obj = {
                        id: this.id,
                        name: this.name,
                        time : new Date().toLocaleString(),
                        isShow:false,
                        num:this.num
                    };
                    this.list.push(obj);
                    this.id = "",
                    this.name = "";
                },
                del(val){
                    //console.log(val);
                    var index = this.list.findIndex(ele=> ele.id == val);
                    console.log(index);
                    this.list.splice(index,1);
                    //alert(val)
                }
            }
}
</script>
